* {
  box-sizing: border-box; }

html,
body {
  margin: 0; }

body {
  color: #ffffff;
  background-color: #374046;
  font-family: 'Open Sans Condensed', sans-serif;
  font-size: 12px;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased; }

a {
  color: #f44336;
  font-weight: bolder;
  text-decoration: none; }

p {
  margin: 0; }
  p ~ p {
    margin-top: 10px; }

.container, .grid.standard {
  zoom: 1; }
  .container:before, .grid.standard:before, .container:after, .grid.standard:after {
    content: '';
    display: table; }
  .container:after, .grid.standard:after {
    clear: both; }

nav ul {
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  margin: 0; }

nav li {
  margin: 0.5px;
  display: block; }

nav a {
  transition: background-color 0.4s ease, color 0.4s ease;
  background-color: #607d8b;
  padding: 8px;
  font-size: 12px;
  color: #ffffff;
  display: block; }
  nav a.selected, nav a:hover {
    background-color: #4f6773; }
  nav a:active, nav a.selected:hover {
    background-color: #475c67; }
  nav a.selected:active {
    background-color: #3f525b; }

.grid.masonry {
  grid-gap: 10px;
  margin: 10px; }
  .grid.masonry.collapse {
    grid-gap: 1px;
    margin: 1px; }

.grid.standard {
  margin: 5px; }
  .grid.standard.collapse {
    margin: 0.5px; }

.item {
  color: #374046;
  background-color: #ffffff; }
  .standard .item {
    float: left;
    width: calc(33.333% - 10px);
    margin: 5px; }
  .standard.collapse .item {
    width: calc(33.333% - 1px);
    margin: 0.5px; }

.title {
  padding: 5px 15px; }
  .title h3 {
    color: #ffffff;
    text-transform: uppercase; }
  .project .title {
    background-color: #f77b7b; }
  .blog .title {
    background-color: #b5b4df; }
  .photo .title {
    background-color: #fedf93; }

.desc {
  padding: 10px 10px 5px 10px; }

